.intro {
  display: flex;
  height: 77vh;
  margin-top: 2rem;
  margin-bottom: 4rem;
  background-color: rgb(90 88 88 / 25%)
}

/* left side styling */
.i-left {
  display: flex;
  position: relative;
  flex: auto;
  flex-direction: column;
  gap: 3rem;
  text-align: center;
  margin-top: 2rem;
  
}

.i-name{
    display: flex;
    flex-direction: column;
}
.i-name > :nth-child(1) {
  color: var(--black);
  font-weight: bold;
  font-size: 3rem;
}
.i-name > :nth-child(2) {
  color: var(--orange);
  font-weight: bold;
  font-size: 3rem;
}

.i-name>:nth-child(3) {
  color: var(--gray);
  font-weight: 100;
  font-size: 14px;
  margin-top: 10px;
  line-height: 1.5;
}
.i-button{
  width:8rem;
  height: 3rem;
}
.i-icons {
  margin-top: 0rem;
  display: flex;
  gap: 0rem;
  justify-content: center;
}
.i-icons > * {
  transform: scale(0.5);
}
.i-icons > *:hover {
  cursor: pointer;
}

.i-right{
    flex:1;
    position: relative
}
.i-right>*{
  position: absolute;
  z-index: 1;
}

.i-right .cus_profile {
  width: 85%;
  height: 510px
}

/* blur */
.blur {
  position: absolute;
  width: 22rem;
  height: 14rem;
  border-radius: 50%;
  background: #edd0ff;
  z-index: -9;
  top: -18%;
  left: 56%;
  filter: blur(72px);
}
@media screen and (max-width: 480px) {
  .intro {
    flex-direction: column;
    gap: 7rem;
    height: 64rem;
  }

  .i-right {
    transform: scale(0.8);
    left: -3rem;
  }
  .i-right .blur {
    display: none;
  }
  .floating-div:nth-of-type(1){
    /* display: none; */
    top: -7rem !important;;
  }
}
@media screen and (max-width: 480px) {
.intro {
  flex-direction: column;
  gap: 7rem;
  height: 50rem;
  margin-bottom: 10rem;
}

.i-right .cus_profile {
  width: 60%;
  position:relative
}
.i-right .blur {
  display: none;
}
.floating-div:nth-of-type(1){
  /* display: none; */
  top: -7rem !important;;
}




}