.card {
    width: 25rem;
    height: 13rem;
    position: absolute;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    width: 20rem;
    text-align: center;
    background: rgba(255, 255, 255, 0.26);
    border: 7px solid var(--orangeCard);
    box-shadow: var(--boxShadow);
    border-radius: 20px;
    padding: 0px 26px 2rem 26px;
  }

  .card span:nth-of-type(2) {
    color: var(--gray);
    font-size: 14px;
  }
  .card > img {
    transform: scale(0.4);
    margin-bottom: -2rem;
    margin-top: -4rem;
  }

@media screen and (max-width: 480px){
  .card{
    width: auto;
  }
}